import React, { FC, memo } from 'react';
import s from './CustomCheckbox.module.scss';

interface CustomInputProps {
  className?: string;
  value?: boolean;
  onChange?: (value: boolean) => void;
  disabled?: boolean;
  label?: string;
}

const CustomCheckbox: FC<CustomInputProps> = memo((props) => {
  const {
    className,
    value = false,
    onChange,
    disabled = false,
    label,
  } = props;

  const onChangeHandler = () => {
    onChange?.(!value);
  };

  return (
    <div className={s.body}>
      <label className={s.check}>
        <input
          type="checkbox"
          checked={value}
          onChange={onChangeHandler}
          style={{ display: 'none' }}
          disabled={disabled}
        />
        <div className={value ? s.checked : s.unchecked}/>
        <div className={s.label}>{label}</div>
      </label>
    </div>
  );
});

export { CustomCheckbox };
