import React, { useState, TextareaHTMLAttributes, memo, ChangeEvent } from 'react';
import s from './CustomInput.module.scss';

type HTMLTextAreaProps = Omit<TextareaHTMLAttributes<
  HTMLTextAreaElement
>, 'value' | 'onChange' | 'readOnly'>

interface CustomTextAreaProps extends HTMLTextAreaProps {
  className?: string;
  value?: string;
  onChange?: (value: string) => void
  type?: 'text' | 'password'
  placeholder?: string;
  autofocus?: boolean
  readOnly?: boolean
  disabled?: boolean
  rows?: number
  background?: 'white' | 'grey';
}

const CustomTextArea = memo((props: CustomTextAreaProps) => {
  const {
    className,
    value,
    onChange,
    type = 'text',
    placeholder,
    autofocus,
    readOnly,
    disabled = false,
    rows = 3,
    background = 'white',
    ...otherProps
  } = props;

  const [isFocused, setIsFocused] = useState<boolean>(false);

  const onChangeHandler = (e: ChangeEvent<HTMLTextAreaElement>) => {
    onChange?.(e.target.value);
  };

  const handleFocus = () => {
    setIsFocused(true);
  };

  const handleBlur = () => {
    setIsFocused(false);
  };

  return (
    <div className={s.inputContainer}>
      <textarea
        className={`${s.input} ${s[background]}`}
        value={value}
        onChange={onChangeHandler}
        disabled={disabled}
        onFocus={handleFocus}
        onBlur={handleBlur}
        rows={rows}
        {...otherProps}
      />
      {placeholder && <div className={`${
        s.customPlaceholder
      } ${
        (isFocused || value) && s.customPlaceholderActive
      } ${
        s[background]
      } ${
        isFocused && s.placeHolderTextActive
      }`}
      >
        {placeholder}
      </div>}
    </div>
  );
});

export { CustomTextArea };
