import s from './ContactUsInfo.module.scss';
import { memo, useContext } from 'react';
import { MiniSiteContext } from 'templatesReact/miniSiteCommon/Context';
import ContactMail from 'images/ContactMail.svg';
import ContactPhone from 'images/ContactPhone.svg';
import ContactAddress from 'images/ContactAddress.svg';

const ContactUsInfo = memo(() => {
  const { data: {
    emailDomain,
    contactNumber,
    address,
  } } = useContext(MiniSiteContext);

  const contactList = [
    { Icon: ContactMail, value: emailDomain },
    { Icon: ContactPhone, value: contactNumber },
    { Icon: ContactAddress, value: address },
  ];

  const contacts = contactList.map(({
    Icon,
    value,
  }) => <div className={s.contactItem} key={value}>
    <Icon className={s.iconItem}/> <span className={s.text}>{value}</span>
  </div>);


  return (
    <div className={s.body}>
      <span className={s.text}>Have questions? We are here for you. Drop us a line, write us an email, or send us a text.</span>
      <div className={s.contactBlock}>{contacts}</div>
    </div>
  );
});

export { ContactUsInfo };